// NOTE: The contents of this file will only be executed if
// you uncomment its entry in "web/static/js/app.js".

// To use Phoenix channels, the first step is to import Socket
// and connect at the socket path in "lib/my_app/endpoint.ex":
import {Socket, Presence} from "phoenix"

// When you connect, you'll often need to authenticate the client.
// For example, imagine you have an authentication plug, `MyAuth`,
// which authenticates the session and assigns a `:current_user`.
// If the current user exists you can assign the user's token in
// the connection for use in the layout.
//
// In your "web/router.ex":
//
//     pipeline :browser do
//       ...
//       plug MyAuth
//       plug :put_user_token
//     end
//
//     defp put_user_token(conn, _) do
//       if current_user = conn.assigns[:current_user] do
//         token = Phoenix.Token.sign(conn, "user socket", current_user.id)
//         assign(conn, :user_token, token)
//       else
//         conn
//       end
//     end
//
// Now you need to pass this token to JavaScript. You can do so
// inside a script tag in "web/templates/layout/app.html.eex":
//
//     <script>window.userToken = "<%= assigns[:user_token] %>";</script>
//
// You will need to verify the user token in the "connect/2" function
// in "web/channels/user_socket.ex":
//
//     def connect(%{"token" => token}, socket) do
//       # max_age: 1209600 is equivalent to two weeks in seconds
//       case Phoenix.Token.verify(socket, "user socket", token, max_age: 1209600) do
//         {:ok, user_id} ->
//           {:ok, assign(socket, :user, user_id)}
//         {:error, reason} ->
//           :error
//       end
//     end
//
// Finally, pass the token on connect as below. Or remove it
// from connect if you don't care about authentication.

window.onload = function () {
  console.log("Loading App...")
}

document.addEventListener("turbolinks:load", function() {
  console.log("Turbolink App...")
  if (document.socket == undefined) {
    document.socket = new Socket("/socket", {params: {guardian_token: document.jwt}})
    document.socket.connect() 
  }

  if (document.ytid == undefined) {
    return
  }

  // Now that you are connected, you can join channels with a topic:
  let channel = document.socket.channel("watch:"+document.ytid, {})
  channel.join()
    .receive("ok", resp => { console.log("Joined successfully", resp) })
    .receive("error", resp => { console.log("Unable to join", resp) })

  // detect if user has joined for the 1st time or from another tab/device
  let onJoin = (id, current, newPres) => {
    if(!current){
      console.log("user has entered for the first time", newPres)
    } else {
      console.log("user additional presence", newPres)
    }
  }
  // detect if user has left from all tabs/devices, or is still present
  let onLeave = (id, current, leftPres) => {
    if(current.metas.length === 0){
      console.log("user has left from all devices", leftPres)
    } else {
      console.log("user left from a device", leftPres)
    }
  }
  let presences = {} // client's initial empty presence state
  channel.on("presence_state", state => {
    presences = Presence.syncState(presences, state, onJoin, onLeave)
    
    	let listBy = (id, {metas: [first, ...rest]}) => {
        first.count = rest.length + 1 // count of this user's presences
        first.id = id
        return first
      }
      let onlineUsers = Presence.list(state, listBy)

    document.getElementsByClassName("alert-info")[0].innerText = "Online Users: "+ onlineUsers.map(function(obj){return obj["id"];}).join(", ")
    setTimeout(function(){ document.getElementsByClassName("alert-info")[0].innerText = "" }, 3000);
    console.log("Presence synced", presences)
  })

  channel.on("presence_diff", diff => {
    presences = Presence.syncDiff(presences, diff, onJoin, onLeave)
    let listBy = (id, {metas: [first, ...rest]}) => {
        first.count = rest.length + 1 // count of this user's presences
        first.id = id
        return first
      }
    let onlineUsers = Presence.list(presences, listBy)
     document.getElementsByClassName("alert-info")[0].innerText = "Neue User: "+onlineUsers.map(function(obj){return obj["id"];}).join(", ")
     setTimeout(function(){ document.getElementsByClassName("alert-info")[0].innerText = "" }, 3000);
     console.log("Presence diff synced", diff)
  })

  channel.on("views_count", payload => {
    document.getElementById("views-count").innerText = "Views: " + payload.views_count
  })

})

export default document.socket
